<script>
  import $ from 'jquery'
  import player from '@/components/musicPlayer.vue'
  export default {
    name: 'introPage',
    components: {
      player,
    },
  data() {
      return {
        songs: {},
        desc: "",
        modal: {
          songname: '',
          desc: ''
        },
        temp: {

        },
        newsong: {
          songname: '',
          cover: '',
          artists: {
            name: '',
            trueid: '',
          },
          desc: '',
          audioshow: false,
          audios: []
        },
        player: player,
        curr: 0,
      }
    },
    methods: {
      lockscr(t){
        if (t == 0) { //禁止滚动
          document.body.style = "overflow:hidden";
          document.body.addEventListener('touchmove', this.bodyScroll, { passive: false });
        } else if (t == 1) { //开启滚动
          document.body.style = "";
          document.body.removeEventListener('touchmove', this.bodyScroll, { passive: false });
          window.onmousewheel = function () {
            return true;
          }
        }
      },
      glryClear() {
        this.modal = {};
        this.temp = {};
      },
      glryUpdate(i) {
        if (i === 0) {
          document.querySelector("#newsongSec").scrollIntoView({
            block: 'end',
            behavior: 'smooth'
          });
        } else {
          if (this.curr !== i) {
            this.modal = {
              cover: this.songs[i].cover,
              songname: this.songs[i].name,
              artists: this.songs[i].artists,
              audioshow: false,
              audios: []
            }
            this.modal.desc = '加载中···'
            fetch(`/api/getAlbum/${this.songs[i].trueid}`).then(r => r.json())
              .then(r => {
                this.modal = {
                  cover: this.songs[i].cover,
                  songname: this.songs[i].name,
                  artists: this.songs[i].artists,
                  audioshow: false,
                  audios: []
                }
                this.temp = (r.resourceState == true) ? r : {}
                this.modal.desc = (r.resourceState == true) ? r.album.description : '__//\n获取失败，请稍后再试···\n- Diary Music -\n//__'
                for (let i = 0; i < r.songs.length; i++) {
                  this.modal.audioshow = true;
                  let arts = []
                  for (let iar = 0; iar < r.songs[i].ar.length; iar++) {
                    arts.push({
                      n: r.songs[i].ar[iar].name,
                      i: r.songs[i].ar[iar].id
                    })
                  }
                  this.modal.audios.push({
                    name: `#${i + 1}: ${r.songs[i].name}`,
                    artists: arts,
                    src: `/api/getMp3/${r.songs[i].id}`,
                  })
                }
              })
              .catch(e =>
                this.modal.desc = `__//\n获取失败，请稍后再试···\n错误代码：${e}\n- Diary Music -\n//__`
              )
          }
          $('#PlayerModal').modal('show')
          this.curr = i
        }
      },
    },
    created() {
      const scriptTags = document.querySelectorAll('.dynaScri');
      scriptTags.forEach((tag) => {
        tag.remove();
      });

    },
    mounted() {
      // loadJs('/next_index/js/mmd-manual.js');
      // loadJs('/next_index/js/nav.js');
      fetch('/api/get163', {
        headers: {
          'Content-Type': 'application/json;charset=utf8',
          'Access-Control-Allow-Origin': '*',
        }
      }).then(d => d.json())
        .then(d => {
          this.newsong = {
            name: '',
            cover: '',
            artists: {
              name: '',
              trueid: '',
            },
            desc: '',
            audioshow: false,
            audios: []
          },
            this.songs = (d.status = 200) ? ((d.data != undefined) ? d.data : {}) : {}
          this.temp = (d.status = 200) ? ((d.data != undefined) ? d.data : {}) : {}
          this.newsong = (d.status = 200) ? ((d.data != undefined) ? d.data[0] : {}) : {}
          this.newsong.desc = '加载中···'
          fetch(`/api/getAlbum/${this.songs[0].trueid}`).then(r => r.json())
            .then(r => {
              this.temp = (r.resourceState == true) ? r : {}
              this.newsong.desc = (r.resourceState == true) ? r.album.description : '__//\n获取失败，请稍后再试···\n- Diary Music -\n//__';
              this.newsong.audios = []
              for (let i = 0; i < r.songs.length; i++) {
                this.newsong.audioshow = true;
                let arts = []
                for (let iar = 0; iar < r.songs[i].ar.length; iar++) {
                  arts.push({
                    n: r.songs[i].ar[iar].name,
                    i: r.songs[i].ar[iar].id
                  })
                }
                this.newsong.audios.push({
                  songname: `#${i + 1}: ${r.songs[0].name}`,
                  artists: arts,
                  src: `/api/getMp3/${r.songs[i].id}.mp3`,
                })
              }
            })
            .catch(e => {
              console.log(e);
              console.trace()
              this.newsong.desc = `__//\n获取失败，请稍后再试···\n错误代码：${e}\n- Diary Music -\n//__`
            })
        })
    },
  }
    var scroll_width = 100;  // 设置每次滚动的长度，单位 px
    var scroll_events = "mousewheel DOMMouseScroll MozMousePixelScroll"; 


</script>
